@import '../../styles/abstracts/variables';

.footer {
  background-color: $background;
  border-radius: 18px;
  padding: 74px 70px 115px;
  margin: 113px -86px 0;
  position: relative;


  @media (max-width: 991px) {
    margin: 82px 0 0;

    padding: 21px 24px 40px;
  }

  &__content {
    z-index: 10;
    position: relative;
  }

  &__about {
    width: 275px;
  }

  &__bg {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    border-radius: 18px;
    top: 0;
    left: 0;
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;
      top: -46px;
      left: 0;
      background-image: url('../../assets/images/footer_1.svg');
      background-size: 876px 331px;
      background-repeat: no-repeat;
      width: 876px;
      height: 331px;


      @media (max-width: 991px) {
        bottom: -9px;
        left: -130px;
        right: auto;
        background-size: 705px 267px;
        width: 705px;
        height: 267px;
      }
    }

    &::after {
      content: '';
      position: absolute;
      bottom: -9px;
      right: 42px;
      background-image: url('../../assets/images/footer_2.svg');
      background-size: 810px 282px;
      background-repeat: no-repeat;
      width: 810px;
      height: 282px;
      z-index: 1;


      @media (max-width: 991px) {
        bottom: -197px;
        left: -369px;
        right: auto;
        background-size: 827px 288px;
        width: 827px;
        height: 288px;
      }
    }
  }

  &__mobile-socials {
    padding-top: 60px;
  }

  &__logo {
    margin-bottom: 67px;


    @media (max-width: 991px) {
      margin-bottom: 0;
    }
  }

  &__copyright {
    font-size: .875rem;

    &-link {
      color: inherit;
      text-decoration: none;

      &:hover {
        text-decoration: none;
        color: $overline;
      }
    }
  }

  &__list {
    padding-top: 96px;


    @media (max-width: 991px) {
      padding-top: 47px;
      flex-wrap: wrap;
    }

    &-container {
      flex: 1;
    }
  }

  &__links {
    list-style: none;
    margin: 0;
    padding: 0 45px 0 0;

    @media (max-width: 991px) {
      padding-right: 16px;
      flex-basis: 50%;
    }

  }

  &__link {
    text-decoration: none;
    font-weight: 400;
    display: block;
    color: rgba($title, .8);
    margin-bottom: 22px;
    white-space: nowrap;
    font-size: .875rem;

    &--primary {
      color: $primary;
      margin-bottom: 24px;
      font-weight: 500;
    }

    &:hover {
      text-decoration: none;
      outline: none;
      color: $overline;
    }
  }

  &::before {
    content: '';
    width: 270px;
    height: 290px;
    position: absolute;
    right: 32px;
    top: -102px;
    background-image: url('../../assets/images/footer/footer-desktop.png');
    background-repeat: no-repeat;
    background-size: contain;

    @media (max-width: 991px) {
      background-image: url('../../assets/images/footer/footer-mobile.png');
      width: 149px;
      height: 158px;
      right: 0;
      top: -72px;

    }

  }
}