.hero {
  padding: 22.5px 0 40px;

  @media (min-width: 576px) {
    padding-top: 120px;
    padding-bottom: 80px;
  }


  &__title.h1 {
    @media (max-width: 576px) {
      margin-top: 0;
      font-size: 1.875rem;
    }
  }

  &__desc {
    margin-bottom: 96px;

    @media (max-width: 576px) {
      font-weight: 400;
      font-size: .875rem;
      margin-bottom: 30px;
    }
  }


  &__concept {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

  }

  &__image {
    position: absolute;
    z-index: 2;


    &--mobile {
      z-index: 10;
      position: relative;
      padding-bottom: 30px;
    }

    &--one {
      z-index: 10;
      position: relative;
    }

    &--two {
      top: -80px;
      right: 50px;
    }

    &--three {
      bottom: -6px;
      right: 6px;
    }

    &--four {
      top: -40px;
      left: 60px;
    }

    &--five {
      right: 28px;
      top: 80px;
    }
    &--six {
      left: 38px;
      bottom: 106px;
    }
  }
}