@import '../../styles/abstracts/variables';

.feature {
  position: relative;
  border-radius: 1.125rem;
  background-color: $background;
  padding: 60px 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  height: 100%;

  @media (max-width: 991px) {
    height: auto;
  }

  &__icon {
    width: 100px;
    height: 100px;
    border-radius: 100px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: center;

    &--primary {
      background-color: $primary;
    }

    &--secondary {
      background-color: $secondary-light;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 1.25rem;
    margin-bottom: 30px;
  }

  &__desc {
    font-size: .875rem;
    font-weight: 500;
    color: $title;
  }
}